.Allstats-container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 0.1em solid;
  padding: 10%;
  gap: 1%;
  border-image: repeating-linear-gradient(
      to bottom right,
      #33475b,
      #0033cc,
      #ff77cc,
      rgb(255, 122, 89)
    )
    20;
}
/* 
#eceef1,
