#ffd700 */

.Allstats-heading {
  justify-content: center;
  color: hotpink;
  font-size: 120%;
  font-weight: bold;
}

.Allstats-Score {
  justify-content: center;
  color: cyan;
  font-size: 120%;
  font-weight: bold;
}

.Allstats-line {
  display: flex;
  flex: 1 1 0;
  justify-content: center;
  gap: 4%;
}
